.wrapper {
    position: relative;
}

.projectText{
    text-decoration: none;
    color: #303e49;
    margin: 5px;
}

.projectText:hover{
    color:#2196f3!important; 
}

.skill{
    padding-left: 10px;
}

.projectinfo{
    margin-left: 20px;
}

.project {
    padding: 25px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    box-shadow: 0 2px 4px rgba(3,27,78,.06);
    border-radius: 10px;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: #f4f5f9;
    text-align: left;
}

.description{
    padding-right: 200px;
}

.project > h3{
    margin-bottom: 15px;
}

.project > h5{
    font-size: 20px;
}

.project:hover {
    color: #1b1d20;
    background-color: #fff;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.collumn {
    padding-left: 100px;
    padding-right: 100px;
    margin-top: 50px;
    background-color: #fff;
}

.wrapper h1,h5 {
    margin-bottom: 10px;
    text-align: left;
    padding: 0px;
}

.image {
    position: absolute;
    right: 8% !important;
    top: 50%;
    transform: translate(0,-50%);
}


@media only screen and (min-width: 600px) {
    .collumn {
        margin-left: 45%;
    }
}

@media only screen and (min-width: 768px) {
    .collumn {
        margin-left: 30%;
    }
}

@media only screen and (min-width: 1150px) {
    .collumn {
        margin-left: 25%;
    }
}